  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  position: relative;
  display: grid;
  grid-template-rows: 1fr min-content;
  min-height: 100%;
  min-width: 320px;
  overflow: hidden;
}

.container {
  margin: 0 auto;
  max-width: 1672px;
  padding: 0 20px;
}
@media (max-width: 480px) {
  .container {
    padding: 0 10px;
  }
}

.no-js .wrapper::before {
  content: "Пожалуйста, разрешите использовать JavaScript! Без него сайт грустит...";
  position: fixed;
  top: -300px;
  left: 0;
  width: 100%;
  padding: 20px;
  z-index: 99999;
  text-transform: uppercase;
  text-align: center;
  font-size: 1.1rem;
  font-weight: 500;
  color: #f1535e;
  background-color: rgba(0, 0, 0, 0.75);
  animation-name: real;
  animation-delay: 3s;
  animation-duration: 7s;
  animation-fill-mode: forwards;
}

@keyframes real {
  0% {
    top: -300px;
  }
  10% {
    top: 0;
  }
  90% {
    top: 0;
  }
  100% {
    top: -300px;
  }
}
