@media (max-width: 1200px) {
  .header__right {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 10000px;
    left: -10000px;
    z-index: 4;
    background-color: #373C3F;
    flex-direction: column;
    justify-content: center;
    transform: scale(0.5);
    opacity: 0;
    border-radius: 100px;
    transition: transform 0.3s, opacity 0.3s, border-radius 0.3s;
  }
}
.header__right_active {
  right: 0;
  left: 0;
