    padding: 15px;
    font-size: 22px;
  }
}

.header__burger {
  position: absolute;
  top: 0px;
  right: -10000px;
  z-index: 6;
  padding: 35px 15px 15px 15px;
  background-color: transparent;
  border: none;
}
.header__burger.open {
  position: fixed;
}
.header__burger span {
  display: block;
  position: absolute;
  height: 1.7px;
  width: 100%;
  border-radius: 15px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
  background-color: #C8AB81;
}
