.form-animate {
  position: relative;
  overflow: hidden;
}
.form-animate:after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 2;
  background: rgba(55, 60, 63, 0.9) url("../img/loading.gif") center/80px no-repeat;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s;
}
.form-animate._sending:after {
  opacity: 1;
  visibility: visible;
}
.form-animate._ok:after {
  content: "Заявка отправлена!";
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
  visibility: visible;
  color: #cbe6cb;
  text-transform: uppercase;
  background-color: rgba(55, 60, 63, 0.9);
  background-image: none;
  font-weight: 600;
}
.form-animate._no:after {
  content: "Произошла ошибка, повторите пожалуйста!";
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
  visibility: visible;
  color: #e4b2b2;
  text-transform: uppercase;
  background-color: rgba(55, 60, 63, 0.9);
  background-image: none;
  font-weight: 600;
}

.modal-video {
  position: fixed;
  top: 0;
  bottom: 0;
  left: -10000px;
  right: 10000px;
  z-index: 4;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(21, 21, 21, 0.7);
  opacity: 0;
  transition: opacity 0.5s;
}

.modal-video_active {
  left: 0;
  right: 0;
  opacity: 1;
}

.modal-video__inner {
  position: relative;
  width: 70vw;
}
@media (max-width: 1200px) {
  .modal-video__inner {
    width: 80vw;
  }
}

.modal-video__video {
  max-width: 100%;
  max-height: 100%;
  width: 100%;
  height: 100%;
  flex-shrink: 1;
}
.modal-video__video video {
  max-width: 100%;
  max-height: 100%;
}

.modal-video .close {
  position: absolute;
  top: -70px;
  right: -20px;
  width: 80px;
  height: 80px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.modal-video .close:after, .modal-video .close:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 40px;
  height: 2px;
  background-color: rgba(255, 255, 255, 0.6);
}
.modal-video .close:before {
  transform: translate(-50%, -50%) rotate(45deg);
}
.modal-video .close:after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
@media (max-width: 1000px) and (orientation: landscape) {
  .modal-video .close {
    top: 0px;
    right: 20px;
    width: 30px;
    opacity: 0.7;
  }
}

.vjs-icon-placeholder:before {
  color: #C8AB81 !important;
}